form {
  position: absolute;
  top: 10px;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid #3535ac;
}

.input {
  height: 18px;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  padding: 10px;
  width: 400px;
  border: 0;
  background-color: #f6f7f9;
}

.submitBtn {
  height: 38px;
  border: 0;
  background-color: #597796;
  color: white;
  cursor: pointer;
  opacity: 0.4;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
}

.submitBtn:hover {
  opacity: 1;
}

.noResMess {
  padding-top: 60px;
  font-size: large;
}
